.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  height: fit-content; }

.footer__tabs {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 140px;
  padding-top: 60px;
  background-color: #004AA8; }

.footer__signIn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #004AA8;
  width: 100%;
  height: 150px; }

.footer__gutter {
  background-color: #00357D;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 90px; }

.footer__gutter__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 8vw;
  opacity: .4; }
  .footer__gutter__info li {
    color: white;
    margin-right: 15px; }

.footer__gutter__social {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 8vw;
  padding-left: 0;
  opacity: .4; }
  .footer__gutter__social a {
    margin-left: 24px;
    color: white; }

.landing__developers__footer__buttons {
  opacity: .8; }

.landing__developers__footer__buttons--createProfile {
  border: 1px solid white;
  color: #1168df;
  background-color: white; }
  .landing__developers__footer__buttons--createProfile:hover {
    background-color: white;
    color: #1168df; }

.landing__developers__footer__buttons--signIn {
  color: white !important;
  margin-right: 2vw;
  cursor: pointer; }

.footer__signIn__tagline {
  color: white;
  margin-left: 8vw;
  opacity: .8; }

.landing__developers__footer__links {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 8vw;
  opacity: .8; }

#footer__mailChimp {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 8vw;
  opacity: .8; }
  #footer__mailChimp input {
    display: inline-block;
    border-radius: 26px;
    border-style: none;
    border: 1px solid #1168df;
    height: 52px;
    max-width: 70%;
    width: 300px;
    padding: 0 25px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background-color: transparent;
    color: #00357D;
    margin-right: 15px;
    color: white; }
    #footer__mailChimp input::placeholder {
      color: white; }
  #footer__mailChimp p {
    color: white;
    margin-bottom: 10px; }
  #footer__mailChimp div {
    display: flex;
    align-items: center;
    justify-content: center; }
  #footer__mailChimp button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
    border-radius: 26px;
    height: 52px;
    background-color: transparent; }
    #footer__mailChimp button:hover {
      background-color: #0950b0; }

.landing__developers__footer__buttons {
  margin-right: 8vw;
  display: flex;
  align-items: center; }

.landing__nav__link--footer {
  margin-left: 38px;
  font-size: 16px;
  color: white; }

@media only screen and (max-width: 600px) {
  .landing__developers__footer__buttons--signIn {
    font-size: 14px !important;
    font-weight: 400;
    margin-right: 10vw; }
  .landing__developers__footer__links__logo {
    margin-bottom: 16px; }
  .landing__developers__footer__links__logoWrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 40px; }
  .landing__nav__link--footer {
    margin-left: 0;
    margin-top: 10px;
    font-size: 14px;
    font-weight: 400; }
  .landing__developers__footer__buttons--createProfile {
    font-size: 14px !important;
    font-weight: 400; }
  .landing__developers__footer__buttons {
    width: 70vw;
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-right: 0; }
  .landing__developers__footer__links {
    margin-left: 0;
    padding: 0; }
  .footer__tabs {
    flex-direction: column;
    height: 180px;
    padding-bottom: 60px;
    padding-top: 40px;
    align-items: flex-start; }
  #footer__mailChimp {
    margin: 0 0 0 19px;
    align-items: center;
    width: 90vw; }
    #footer__mailChimp input {
      width: 60vw;
      max-width: 80vw; }
  .footer__signIn__tagline {
    display: none; }
  .footer__signIn {
    display: none; }
  .landing__developers {
    margin-bottom: 380px; }
  .footer__gutter {
    flex-direction: column-reverse;
    height: 120px;
    padding: 24px 0 24px 36px;
    justify-content: center;
    align-items: flex-start; }
  .footer__gutter__info {
    padding: 12px 0;
    margin-left: 24px; }
  .footer__gutter__social {
    padding: 12px 0; } }
